import React from "react";
import { Heading, LightText } from "../CommonStyles/styles";

const About = () => {
  return (
    <div className="mt-5 f1 text-center">
      <Heading size={2}>About Me</Heading>
      <div className="row m-0">
        <div className="col">
          <blockquote class="blockquote ">
            <p>
              Solving critical problems using the idea of{" "}
              <b>“ reasoning from first principles ” </b>
              is way more exciting than anything for me . As humans that's what we have always done.
            </p>
            <p>
              We are stardust and our lifetime in <b> “ this universe ” </b> is very short and
              fragile, so lets make the most of it.
            </p>
            <p>
              I Look at everything as a probabilistic event to relieve myself from pressure and
              anxiety, it is very usefull when you know all the <b>“ variables ” </b>
            </p>
            <br />
            <footer class="blockquote-footer">Myself</footer>
          </blockquote>
        </div>
      </div>
    </div>
  );
};

export default About;
