import React from "react";
import { SocialIcons, MenuLinks } from "../CommonStyles/styles";
import gmail from "../../assets/img/gmail.svg";
import linkedin from "../../assets/img/linkedin.svg";
import portfolio from "../../assets/img/portfoliotwo.svg";
import home from "../../assets/img/home.svg";
import { Link } from "react-router-dom";

const menu = [
  {
    text: "",
    to: "/",
    type: "route",
    icon: home,
    className: "mview",
  },
  {
    text: "My Works",
    to: "/works",
    type: "route",
    icon: null,
    className: "mview",
  },
  // {
  //   text: "Skills",
  //   to: "/#skills",
  //   type: "route",
  //   icon: null,
  // },
  {
    text: "Resume",
    to: "/resume",
    type: "route",
    icon: null,
  },
];

const socialLinks = [
  {
    text: "LinkedIn",
    to: "",
    type: "link",
    icon: linkedin,
    link: "https://www.linkedin.com/in/saravanan-ayyappan-27440016a/",
  },
  {
    text: "Gmail",
    to: "",
    type: "clipboard",
    icon: gmail,
    value: "asaravanan248@gmail.com",
  },
];

const handleSocialIcon = (event, s) => {
  if (s.type === "clipboard") {
    event.stopPropagation();
    let input = document.createElement("input");
    input.value = s.value;
    input.setSelectionRange(0, 99999);
    document.execCommand("copy");
    console.log(event.target, document.execCommand("copy"));
  }

  if (s.type === "link") {
    window.open(s.link);
  }
};

const Header = () => {
  return (
    <>
      <div className="d-flex justify-content-between align-items-center f1 bg-light">
        <div className="d-flex">
          {menu.map((m) => {
            return (
              <Link to={m.to}>
                <MenuLinks className={m.className ? m.className : ""}>
                  <span className="menuTxt">
                    {m.text}
                    {m.icon && <img src={m.icon} width={20} height={20} />}
                  </span>
                </MenuLinks>
              </Link>
            );
          })}
        </div>
        <div className="d-flex">
          {socialLinks.map((s) => {
            return (
              <SocialIcons data-text={s.value} onClick={(e) => handleSocialIcon(e, s)}>
                {s.icon && <img src={s.icon} data-text={s.value} className="menuImg" />}
              </SocialIcons>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Header;
