import React from "react";
import { Heading, GrayImg, LightText } from "../CommonStyles/styles";
import htmlcss from "../../assets/img/htmlcss.png";
import js from "../../assets/img/js.png";
import react from "../../assets/img/react.webp";
import angular from "../../assets/img/angular.svg";
import node from "../../assets/img/node.png";
import php from "../../assets/img/php.svg";
import java from "../../assets/img/java.png";
import mongodb from "../../assets/img/mongo.png";
import mysql from "../../assets/img/mysql.svg";
import d3 from "../../assets/img/d3.png";
import konva from "../../assets/img/konva.png";
import tokbox from "../../assets/img/tokbox.png";

const techArr = [
  {
    src: htmlcss,
    w: 130,
    h: 100,
  },
  {
    src: js,
    w: 100,
    h: 100,
  },
  {
    src: react,
    w: 100,
    h: 100,
  },
  { src: angular, w: 100, h: 100 },
  { src: node, w: 130, h: 100 },
  { src: php, w: 130, h: 100 },
  { src: java, w: 80, h: 120 },
  { src: mysql, w: 190, h: 100 },
  { src: mongodb, w: 250, h: 80 },
];
const lib = [
  { src: d3, w: 50, h: 50, title: "d3" },
  { src: konva, w: 50, h: 50, title: "konva" },
  { src: tokbox, w: 50, h: 50, title: "tokbox" },
];

const Tech = () => {
  return (
    <div className="text-center mt-5 f1">
      <Heading size={2} className="">
        Tech
      </Heading>
      <hr></hr>{" "}
      <div className="row m-0 p-2">
        {techArr.map((t) => {
          return (
            <div className="col">
              <GrayImg src={t.src} w={t.w} h={t.h} className="p-2"></GrayImg>
            </div>
          );
        })}
      </div>
      <hr></hr>
      <div className="row m-0 p-2">
        {lib.map((l) => {
          return (
            <div className="col">
              <GrayImg title={l.title ? l.title : ""} src={l.src} w={l.w} h={l.h}></GrayImg>
            </div>
          );
        })}
      </div>{" "}
      <hr></hr>
    </div>
  );
};

export default Tech;
