import React from "react";
import { Heading, LightText } from "../CommonStyles/styles";

const worksObj = [
  {
    name: "Modeller",
    role: "Project Lead & FullStack Developer",
    desc:
      "A Dynamic application with 4 types of automated modelling tools for design automation design for processess, mindmaps, KPI's and DB schema.",
    tech: ["React JS", "d3", "Canvas [React Konva]", "React DND", "Redux", "Node JS", "MongoDB"],
  },
  {
    name: "SQE",
    role: "Project Lead & Frontend Developer",
    desc: "Application for constructing complex SQL Queries using vusializations.",
    tech: ["React JS", "d3", "Canvas [React Konva]", "React DND", "Redux"],
  },
  {
    name: "BINCO",
    role: "Frontend Developer",
    desc:
      "A Business Information application (BINCO) - facilitating relationships among companies.",
    tech: ["Angular JS"],
  },
  {
    name: "Volini MaxCheer",
    role: "Frontend Developer",
    desc: "A world cup campaign microsite built in under 4 days for Volini (Sun pharmaceuticals)",
    tech: ["React JS", "tokbox"],
  },
  {
    name: "Optacredit",
    role: "Frontend Developer",
    desc: "Complete Loan management solutions.",
    tech: ["React JS", "Recharts", "Redux"],
  },
  {
    name: "SuvidaCare",
    role: "Frontend Developer",
    desc: "Care Management Application.",
    tech: ["Angular 6"],
  },
  {
    name: "75Health",
    role: "Fullstack Developer & Research",
    desc: "An EHR software for recording patient helath records (MU2 certified , HIPPA).",
    tech: ["Bootstrap 3", "Jquery", "JAVA", "MySql"],
    links: [{ name: "https://www.75health.com/", url: "https://www.75health.com/" }],
  },
  {
    name: "DemoErp",
    role: "Fullstack Developer & Research",
    desc:
      "A ERP Software that facilitates businesses in Purchase and Sales ManagementCare Management Application.",
    tech: ["Bootstrap 4", "Jquery", "PHP", "MySql"],
  },
  {
    name: "Juno",
    role: "Fullstack Developer & Research",
    desc:
      "A Stock Market Application Using Angular 1.X which extracts live price quote of stocks and applies a algorithm to predict price movements and suggest trade setups.",
    tech: ["Bootstrap 4", "Jquery", "PHP", "MySql"],
  },
  {
    name: "CodeSnipp",
    role: "Fullstack Developer & Research",
    desc: "A Simple Code repository with a chrome extension to save snippets from the web.",
    tech: ["Bootstrap 4", "Jquery", "PHP", "MySql"],
  },
];

const Works = () => {
  return (
    <div className="container f1">
      {worksObj.map((work, wIndx) => {
        return (
          <>
            <div className="row">
              <div className="col col-lg-3"></div>
              <div className="col-10 col-lg-6 text-center">
                <div>
                  <Heading size={2}>{work.name}</Heading>
                  <p className="text-primary">
                    <b>{work.role}</b>
                  </p>
                  <LightText>{work.desc}</LightText>
                  {work.tech.map((t, index) => {
                    return (
                      <>
                        <small>{t}</small>
                        {index + 1 !== work.tech.length ? " - " : ""}
                      </>
                    );
                  })}
                  {work.links &&
                    work.links.length > 0 &&
                    work.links.map((l) => {
                      return (
                        <p>
                          <a target="_blank" href={l.url}>
                            {l.name}
                          </a>
                        </p>
                      );
                    })}
                </div>
              </div>{" "}
              <div className="col col-lg-3"></div>
            </div>
            <hr></hr>
          </>
        );
      })}
    </div>
  );
};

export default Works;
